

.stuff_wrapper{
    border-top: rgba(200,200,200,0.8) solid 1px;
    width: 100%;
    padding: 120px 0 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.stuff_container{
    width: var(--fullwidth);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 80px;
}

.stuff_container>p{
    font-size: 46px;
    font-weight: 800;
    color: #232F55;
}

.stuff_items{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

@media only screen and (max-width: 1200px){
    .stuff_container{
        padding: 0 20px;
        width: var(--minwidth);
    }

    .stuff_items{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 40px;
    }
}



@media only screen and (max-width: 900px){
    .stuff_container{
        padding: 0 20px;
        width: var(--minwidth);
    }

    .stuff_items{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;
    }
}


